@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/roboto-400.woff2") format("woff2"), url("../fonts/roboto-400.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/roboto-500.woff2") format("woff2"), url("../fonts/roboto-500.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/roboto-700.woff2") format("woff2"), url("../fonts/roboto-700.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: contain;
  user-select: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.centralizer {
  width: 1200px;
  margin: 0 auto;
  padding-left: 45px;
  padding-right: 45px;
}

.button {
  display: inline-block;
  max-width: 300px;
  margin: 0;
  padding: 10px 27px;
  font-family: inherit;
  line-height: 1.2;
  text-align: center;
  word-wrap: break-word;
  color: #000000;
  border-radius: 40px;
  border: 2px solid #66c05d;
  background-color: transparent;
  transition: all 0.2s;
  cursor: pointer;
  user-select: none;
}
.button:hover {
  color: #ffffff;
  background-color: #66c05d;
}
.button:active {
  color: #000000;
  background-color: transparent;
  opacity: 0.7;
}
.button--secondary {
  letter-spacing: 0.05em;
  color: #ffffff;
  border-color: #ffffff;
  background-color: #66c05d;
}
.button--secondary:hover {
  color: #66c05d;
  border-color: rgba(102, 192, 93, 0.5);
  background-color: #ffffff;
}
.button--secondary:active {
  color: #ffffff;
  border-color: #ffffff;
  background-color: #66c05d;
  opacity: 0.7;
}

.call-back {
  display: flex;
  align-items: center;
}

.call-back__number {
  margin-right: 40px;
  font-size: 1.14rem;
  line-height: 1.25;
  color: inherit;
  transition: opacity 0.1s;
}
.call-back__number:hover {
  opacity: 0.5;
}
.call-back__number:active {
  opacity: 0.7;
}

.field {
  min-width: 225px;
  margin: 0;
  padding: 7px 25px;
  font: inherit;
  line-height: 1.2;
  color: #333333;
  border: 2px solid transparent;
  border-radius: 40px;
  background-color: #ffffff;
  outline: none;
  transition: all 0.1s;
}
.field:hover {
  background-color: #efefef;
}
.field:focus {
  border-color: rgba(102, 192, 93, 0.5);
  background-color: #ffffff;
}
.field::placeholder {
  color: #c4c4c4;
}

.socials {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(4, 45px);
  gap: 10px 20px;
}

.socials__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  color: #66c05d;
  border-radius: 50%;
  border: 2px solid #66c05d;
  outline: none;
  transition: all 0.2s;
}
.socials__link:hover, .socials__link:focus-visible {
  color: #ffffff;
  background-color: #66c05d;
}
.socials__link:active {
  color: #66c05d;
  background-color: transparent;
  opacity: 0.7;
}

.page {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  line-height: 1.43;
  height: 100%;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-width: 1200px;
  min-height: 100%;
  margin: 0 auto;
}

.page__main {
  flex-grow: 1;
}

.secondary-navigation {
  line-height: 1.42;
  color: #333333;
  background-color: #efefef;
}

.secondary-navigation__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  list-style-type: none;
}

.secondary-navigation__link {
  display: block;
  padding: 17px 15px;
  max-width: 170px;
  color: inherit;
  word-wrap: break-word;
  transition: opacity 0.1s;
}
.secondary-navigation__link:hover {
  opacity: 0.5;
}
.secondary-navigation__link:active {
  opacity: 0.7;
}

.navigation {
  font-size: 1.14rem;
  line-height: 1.25;
  border-top: 1px solid #efefef;
}

.navigation__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  list-style-type: none;
}

.navigation__link {
  display: block;
  padding: 30px 20px;
  max-width: 190px;
  color: inherit;
  word-wrap: break-word;
  transition: opacity 0.1s;
}
.navigation__link:hover {
  opacity: 0.5;
}
.navigation__link:active {
  opacity: 0.7;
}

.header__row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 20px;
}

.promo {
  color: #ffffff;
  background-color: #66c05d;
  overflow: hidden;
}

.promo__container {
  display: grid;
  grid-template-columns: 410px 1fr;
  column-gap: 50px;
  align-items: center;
}

.promo__title {
  margin: 0;
  margin-bottom: 50px;
  font-size: 3.43rem;
  line-height: 1.35;
}

.promo__text {
  margin: 0;
  margin-bottom: 15px;
  max-width: 310px;
}

.promo__link {
  margin-top: 35px;
  padding-right: 45px;
  padding-left: 45px;
}

.promo__image-container {
  position: relative;
}
.promo__image-container::before, .promo__image-container::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background-color: #ffffff;
  opacity: 0.3;
}
.promo__image-container::before {
  width: 860px;
  height: 860px;
}
.promo__image-container::after {
  width: 680px;
  height: 680px;
  opacity: 0.4;
}

.promo__image {
  position: relative;
  z-index: 1;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 255px;
  padding: 25px 30px;
  background-color: #ffffff;
}

.card__image-link {
  order: -1;
}

.card__title {
  margin: 0;
  margin-bottom: 20px;
  font-size: 1rem;
  font-weight: 400;
  text-align: center;
  color: #333333;
}

.card__price {
  margin-bottom: 20px;
  font-size: 1.5rem;
  font-weight: 500;
}

.card__button {
  margin-top: auto;
  padding: 8px 40px;
  border-radius: 30px;
  border: none;
}

